import React, { useEffect, useState } from "react";

import "./style.scss";

const preText = string => {
  const pattern = ["photos", "video", "posts"];
  let firstOctet = "";
  let secondOctet = "";

  if (!string) {
    return "";
  }

  if (string.indexOf(pattern[0]) !== -1) {
    console.log("0");
    firstOctet = string.split("/a.")[0]?.replace(pattern[0], pattern[2]);
    secondOctet = string.split("/a.")[1]?.split("/")[1];
    return `${firstOctet}/${secondOctet}`;
  }

  if (string.indexOf(pattern[1]) !== -1) {
    console.log("1");
    firstOctet = string.split("/a.")[0]?.replace(pattern[1], pattern[2]);
    secondOctet = string.split("/a.")[1]?.split("/")[1];
    return `${firstOctet}/${secondOctet}`;
  }

  if (string.indexOf(pattern[2]) !== -1) {
    console.log("2");
    firstOctet = string?.split("?")[0];
    return firstOctet;
  }

  return "";
};

export const Convert = () => {
  const [isText, setIsText] = useState("");
  const [isConvertText, setIsConvertText] = useState("");

  useEffect(() => {
    isText && setIsConvertText(preText(isText));
  }, [isText]);

  useEffect(() => {
    if (isConvertText) {
      window.navigator.clipboard.writeText(isConvertText);
    }
  }, [isConvertText]);

  return (
    <>
      <div className="convert">
        <div className="convert-input">
          <div>полная ссылка</div>
          <input
              placeholder={'Вставь сюда полную ссылку'}
            type="text"
            onChange={event => setIsText(event.currentTarget.value)}
            value={isText}
          />
        </div>
        <div className="convert-input">
          <div>короткая ссылка</div>
          <div className="convert-label">{isConvertText || "В этом месте будет готовая ссылка"}</div>
        </div>
      </div>
      <div className='footer'>
        <div>После вставки полной ссылки, готовая ссыкла копируется в буффер автоматически</div>
        <a href="tg://resolve?domain=codernd">телега: @codernd</a>
      </div>
    </>
  );
};
